import { setAudioset } from "../../playback/actions";
import { LOAD_AUDIOSET, setAudiosetIndex, INIT_SIDE_EFFECTS } from "../model";
import { fetchAudioset, fetchIndex } from "../../audioset";

export default function effects(action, { dispatch }) {
  switch (action.type) {
    case INIT_SIDE_EFFECTS:
      fetchIndex().then(index => dispatch(setAudiosetIndex(index)));
      break;
    case LOAD_AUDIOSET:
      fetchAudioset(action.id).then(audioset =>
        dispatch(setAudioset(audioset))
      );
      break;
    default:
      break;
  }
}
