import { combineReducers } from "redux";
import playback from "../playback";
import { SYNC_PLAYBACK } from "../playback/actions";

export const INIT_SIDE_EFFECTS = "INIT_SIDE_EFFECTS";

const sa = name => "/session/" + name;
export const SET_AUDIOSET_INDEX = sa("SET_AUDIOSET_INDEX");
export const LOAD_AUDIOSET = sa("LOAD_AUDIOSET");
export const TOGGLE_IS_CONTROL = sa("TOGGLE_IS_CONTROL");

export const setAudiosetIndex = index => ({ type: SET_AUDIOSET_INDEX, index });
export const toggleIsControl = () => ({ type: TOGGLE_IS_CONTROL });
// side effects
export const loadAudioset = id => ({ type: LOAD_AUDIOSET, id });

const initialSessionState = {
  audiosetIndex: null,
  isControl: false
};

function session(state = initialSessionState, action) {
  switch (action.type) {
    case SET_AUDIOSET_INDEX:
      return { ...state, audiosetIndex: action.index };
    case TOGGLE_IS_CONTROL:
      return { ...state, isControl: !state.isControl };
    default:
      return state;
  }
}

const initialRoomState = {
  user: null,
  room: null,
  roomList: []
};

const ra = name => "/sync/" + name;
export const SET_LOGGED_IN = ra("SET_LOGGED_IN");
export const CREATE_ROOM_REQUEST = ra("CREATE_ROOM_REQUEST");
export const SET_CURRENT_ROOM = ra("SET_CURRENT_ROOM");
export const SET_ROOM_LIST = ra("SET_ROOM_LIST");
export const REQUEST_ROOM = ra("REQUEST_ROOM");

export const setLoggedIn = user => ({ type: SET_LOGGED_IN, user });
export const createRoom = () => ({ type: CREATE_ROOM_REQUEST });
export const setRoom = room => ({ type: SET_CURRENT_ROOM, room });
export const setRoomList = roomList => ({ type: SET_ROOM_LIST, roomList });
export const joinRoom = room => ({ type: REQUEST_ROOM, room });

function sync(state = initialRoomState, action) {
  switch (action.type) {
    case SET_LOGGED_IN:
      return {
        ...state,
        user: action.user,
        room:
          state.room && state.room.id === action.user.roomId ? state.room : null
      };
    case SET_CURRENT_ROOM:
      return { ...state, room: action.room };
    case SET_ROOM_LIST:
      return {
        ...state,
        roomList: action.roomList
      };
    default:
      return state;
  }
}

const initialDesktopState = {
  session: true,
  pads: false,
  tracks: false,
  help: false,
  fullscreen: false
};
const da = name => "/display/" + name;
export const TOGGLE_SESSION = da("SHOW_SESSION");
export const TOGGLE_TRACKS = da("TOGGLE_TRACKS");
export const TOGGLE_PADS = da("TOGGLE_PADS");
export const SHOW_HELP = da("SHOW_HELP");
export const HIDE_HELP = da("HIDE_HELP");
export const SHOW_FULLSCREEN = da("SHOW_FULLSCREEN");
export const SET_FULLSCREEN = da("SET_FULLSCREEN");
export const SET_WINDOW_SIZE = da("SET_WINDOW_SIZE");

export const setSize = (width, height) => ({
  type: SET_WINDOW_SIZE,
  size: { width, height }
});
export const toggleSession = () => ({ type: TOGGLE_SESSION });
export const toggleTracks = () => ({ type: TOGGLE_TRACKS });
export const togglePads = () => ({ type: TOGGLE_PADS });
export const showHelp = () => ({ type: SHOW_HELP });
export const hideHelp = () => ({ type: HIDE_HELP });
export const showFullscreen = () => ({ type: SHOW_FULLSCREEN });
export const setFullscreen = isFullscreen => ({
  type: SET_FULLSCREEN,
  isFullscreen
});

function display(state = initialDesktopState, action) {
  switch (action.type) {
    case SET_WINDOW_SIZE:
      return { ...state, type: action.size.width < 700 ? "mobile" : "desktop" };
    case SYNC_PLAYBACK:
    case TOGGLE_SESSION:
      return { ...state, session: !state.session };
    case SHOW_HELP:
      return { ...state, help: true };
    case HIDE_HELP:
      return { ...state, help: false };
    case TOGGLE_PADS:
      return { ...state, pads: !state.pads };
    case TOGGLE_TRACKS:
      return { ...state, tracks: !state.tracks };
    case SET_FULLSCREEN:
      return { ...state, fullscreen: action.isFullscreen };
    default:
      return state;
  }
}

export default combineReducers({ playback, session, display, sync });
