import {
  SYNC_PLAYBACK,
  START_AUDIO_CLIP,
  STOP_AUDIO_CLIP
} from "../../playback/actions";
import { INIT_SIDE_EFFECTS, SET_WINDOW_SIZE } from "../model";

import Visuals from "../../visuals";

let visuals;

const isVisible = state => state.display.type === "desktop";

export default function effects(action, { getState }) {
  switch (action.type) {
    case INIT_SIDE_EFFECTS:
      visuals = Visuals.init();
      break;

    case SET_WINDOW_SIZE:
      visuals.setVisible(isVisible(getState()));
      break;

    case SYNC_PLAYBACK:
      visuals.setAudioset(action.playback.audioset);
      visuals.setVisible(isVisible(getState()));
      break;

    case START_AUDIO_CLIP:
      visuals.startClip(action.clipId);
      break;

    case STOP_AUDIO_CLIP:
      visuals.stopClip(action.clipId);
      break;

    default:
      break;
  }
}
