import { connect } from "react-redux";
import Tracks from "./Tracks";
import {
  changeTrackVolume,
  pressPad,
  releasePad
} from "../../playback/actions";

const mapStateToProps = state => ({
  audioset: state.playback.audioset,
  status: state.playback.audio.status,
  volumes: state.playback.tracks.volumes,
  playing: state.playback.tracks.playing
});

const mapDispatchToProps = dispatch => ({
  onVolumeChanged: (track, value) => dispatch(changeTrackVolume(track, value)),
  onPress: pad => dispatch(pressPad(pad)),
  onRelease: pad => dispatch(releasePad(pad))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Tracks);
