import { bindActionCreators } from "redux";
import { connect } from "react-redux";

import Toolbar from "./Toolbar";
import {
  showHelp,
  hideHelp,
  toggleSession,
  togglePads,
  toggleTracks,
  showFullscreen,
  toggleIsControl
} from "../../store/model";

const actions = {
  toggleSession,
  showHelp,
  hideHelp,
  togglePads,
  toggleTracks,
  showFullscreen,
  toggleIsControl
};

const mapStateToProps = state => ({
  audioset: state.playback.audioset,
  isControl: state.session.isControl,
  display: state.display,
  isDesktop: state.display.type === "desktop"
});

const mapDispatchToProps = dispatch => bindActionCreators(actions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Toolbar);
