import cxs from "cxs/component";

const Layout = cxs("div")({
  display: "flex",
  flexDirection: "column"
});

const Panel = cxs(Layout)({
  backgroundColor: "black",
  padding: "1em"
});

Panel.Layout = Layout;

Panel.Section = Panel.Header = Panel.Footer = cxs("div")({
  backgroundColor: "black"
});

Panel.Content = Panel.Main = cxs("div")({
  flexGrow: 1
});

export default Panel;
