import Resources from "../audioset/resources";
import Sampler from "./Sampler";
import {
  startAudioClip,
  setAudioStarted,
  stopAudioClip,
  setAudioStopped
} from "../playback/actions";

const mapDispatchToEvents = dispatch => ({
  audioStarted: time => dispatch(setAudioStarted(time)),
  audioStopped: time => dispatch(setAudioStopped(false, time)),
  audioClipStarted: (clipId, trackId, time) =>
    dispatch(startAudioClip(clipId, trackId, time)),
  audioClipStopeed: (clipId, trackId, time) =>
    dispatch(stopAudioClip(clipId, trackId, time))
});

export default class AudioEngine {
  constructor(dispatch) {
    this.events = mapDispatchToEvents(dispatch);
    this.currentSampler = null;
    this.samplers = {};
  }

  restoreState(playback) {
    const { audioset } = playback;
    if (audioset) {
      changeSampler(this, audioset);
      // Eventually, we have to do it better
      this.currentSampler.stopAll();
    }
  }

  playSample(clipId) {
    if (!this.currentSampler) return console.warn("NO sampler!");
    this.currentSampler.play(clipId);
  }

  stopSample(id, events) {
    if (!this.currentSampler) return console.warn("NO sampler!");
    this.currentSampler.stop(id);
  }

  stopAll() {
    if (this.currentSampler) this.currentSampler.stopAll();
  }
}

// PRIVATE //

function getSampler(engine, audioset) {
  const { id } = audioset;
  if (engine.samplers[id]) return engine.samplers[id];
  engine.samplers[id] = new Sampler(audioset, engine.events);
  return engine.samplers[id];
}

function changeSampler(engine, audioset) {
  if (audioset) {
    const sampler = getSampler(engine, audioset);
    sampler.buffers = Resources.getCachedResources(audioset, "audio");
    engine.currentSampler = sampler;
  } else {
    engine.currentSampler = null;
  }
}
