import {
  CREATE_ROOM_REQUEST,
  INIT_SIDE_EFFECTS,
  setRoom,
  setRoomList,
  setLoggedIn,
  REQUEST_ROOM,
  SET_CURRENT_ROOM
} from "../model";
import { syncPlayback, PLAYBACK_PREFIX } from "../../playback/actions";

const send = (ws, msg) => ws.send(JSON.stringify(msg));

function init({ dispatch, getState }) {
  const ws = new WebSocket("ws://localhost:8080");

  ws.onopen = () => {
    const state = getState();
    const { user } = state.sync;
    send(ws, { action: "login", payload: user && user.id });
  };

  ws.onmessage = event => {
    const data = JSON.parse(event.data);
    console.log("RECEIVED", data);
    switch (data.event) {
      case "@sync/loggedIn":
        return dispatch(setLoggedIn(data.payload));
      case "@sync/roomList":
        return dispatch(setRoomList(data.payload));
      case "@sync/requestJoin":
        send(ws, { action: "acceptJoin", payload: data.payload.id });
        const action = syncPlayback(getState().playback);
        send(ws, { action: "broadcastAction", payload: action });
        return;
      case "@sync/joined":
        return dispatch(setRoom(data.payload));
      case "@sync/action":
        // FIXME: filter actions!
        return dispatch(Object.assign(data.payload, { clientId: data.from }));
      default:
        return;
    }
  };
  return ws;
}

function broadcastAction(ws, action, store) {
  const isInternalAction = !action.clientId;
  const isPlayback = action.type.startsWith(PLAYBACK_PREFIX);
  const isWebSocketOpen = ws.readyState === WebSocket.OPEN;

  if (isInternalAction && isPlayback && isWebSocketOpen) {
    send(ws, { action: "broadcastAction", payload: action });
  }
}

export default function(url) {
  let ws;
  let isRoom = false;
  return function(action, store) {
    isRoom && broadcastAction(ws, action, store);
    switch (action.type) {
      case INIT_SIDE_EFFECTS:
        ws = init(store);
        break;
      case CREATE_ROOM_REQUEST:
        ws.send(JSON.stringify({ action: "createRoom" }));
        break;
      case REQUEST_ROOM:
        ws.send(
          JSON.stringify({ action: "requestJoin", payload: action.room.id })
        );
        break;
      case SET_CURRENT_ROOM:
        isRoom = true;
        break;

      default:
        break;
    }
  };
}
