import React from "react";
import ReactDOM from "react-dom";
import { Provider } from "react-redux";
import { setAudioset } from "./playback/actions";
import App from "./ui/App";
import Modal from "react-modal";
import createStore from "./store/createStore";

// const ENV = process.env.NODE_ENV;
// const WEBSOCKETS_URL = "http://localhost:4000";
const store = createStore();

// trigger the current audioset action
const state = store.getState();
const { audioset } = state.playback;
if (state.playback.audioset) {
  store.dispatch(setAudioset(audioset));
}

const element = document.getElementById("app");
Modal.setAppElement(element);
ReactDOM.render(
  <Provider store={store}>
    <App />
  </Provider>,
  element
);
