import React from "react";
import cxs from "cxs/component";
import { Icon } from "../shared";

const activeColor = isActive => (isActive ? "white" : "gray");

export default ({
  audioset,
  isControl,
  isDesktop,
  display,
  toggleSession,
  showHelp,
  togglePads,
  toggleTracks,
  toggleIsControl,
  showFullscreen
}) => (
  <Toolbar>
    <div>{isDesktop && <Icon icon="fly" onClick={showHelp} />}</div>
    <Connection id="Connection" onClick={toggleSession}>
      {audioset.meta.title}
    </Connection>
    {isDesktop && (
      <Transport id="Transport">
        {<Icon icon="stop" />}
        <Icon icon="mute" />
      </Transport>
    )}
    {isDesktop && (
      <Actions>
        <Icon
          icon="mute"
          onClick={toggleIsControl}
          color={isControl ? "gray" : "green"}
        />
        <Icon
          icon="controls"
          color={activeColor(display.pads)}
          onClick={togglePads}
        />
        <Icon
          icon="controls"
          color={activeColor(display.tracks)}
          onClick={toggleTracks}
        />
        <Icon
          icon="cinema"
          color={activeColor(display.fullscreen)}
          onClick={showFullscreen}
        />
      </Actions>
    )}
  </Toolbar>
);

const Transport = cxs("div")();
const Actions = cxs("div")();
const Connection = cxs("div")({
  marginTop: "3px",
  flexGrow: 1,
  textAlign: "center"
});

const Toolbar = cxs("div")({
  display: "flex",
  flexDirection: "row",
  backgroundColor: "black",
  padding: "2px 10px"
});
