import { combineReducers } from "redux";

import {
  SET_AUDIO_STARTED,
  START_AUDIO_CLIP,
  STOP_AUDIO_CLIP,
  AUDIO_STOP_ALL,
  SYNC_PLAYBACK,
  CHANGE_TRACK_VOLUME,
  SET_AUDIO_STOPPED
} from "./actions";

/**
 * Audioset session state
 */
const initialAudiosetState = null;

export function audioset(state = initialAudiosetState, action) {
  switch (action.type) {
    case SYNC_PLAYBACK:
      return action.playback.audioset;
    default:
      return state;
  }
}

/**
 * Audioset session state
 */
const initialAudioState = {
  isPlaying: false,
  status: "STOP",
  startedAt: null
};

export function audio(state = initialAudioState, action) {
  switch (action.type) {
    case SET_AUDIO_STARTED:
      return { ...state, status: "PLAYING", startedAt: action.time };
    case SET_AUDIO_STOPPED:
      return { ...state, status: "STOP", startedAt: null };
    case START_AUDIO_CLIP:
    case STOP_AUDIO_CLIP:
      return {
        ...state,
        status: { ...state.status, [action.clipId]: action.status }
      };
    case AUDIO_STOP_ALL:
      return { ...state, status: {} };
    default:
      return state;
  }
}

export function tracks(state = initTracksState(), action) {
  switch (action.type) {
    case SYNC_PLAYBACK:
      return initTracksState(action.playback.audioset);
    case START_AUDIO_CLIP:
    case STOP_AUDIO_CLIP:
      return changeTrackStatus(state, action.trackId, action.status);
    case CHANGE_TRACK_VOLUME:
      return changeVolume(state, action.trackId, action.value);
    default:
      return state;
  }
}

const valueByTrack = (audioset, value) =>
  audioset.tracks.reduce((obj, track) => {
    obj[track.id] = value;
    return obj;
  }, {});

function initTracksState(audioset) {
  return audioset
    ? {
        playing: valueByTrack(audioset, 0),
        volumes: valueByTrack(audioset, 75)
      }
    : { playing: {}, volumes: {} };
}

function changeTrackStatus(state, trackId, status) {
  const dir = status.isPlaying ? 1 : -1;
  return {
    ...state,
    playing: {
      ...state.playing,
      [trackId]: state.playing[trackId] + dir
    }
  };
}

function changeVolume(state, trackId, value) {
  return {
    ...state,
    volumes: { ...state.volumes, [trackId]: value }
  };
}

export default combineReducers({
  audioset,
  audio,
  tracks
});
