import React from "react";
import PropTypes from "prop-types";
import { Button } from "../shared";

const HasSession = ({ audioset, onCloseAudioset, room, onRequestRoom }) => (
  <div className="Session">
    <h1>{audioset.meta.title}</h1>
    <p>{audioset.meta.description}</p>
    {room ? (
      <h2>Room: {room.name}</h2>
    ) : (
      <Button onClick={onRequestRoom}>Request room</Button>
    )}
    <Button onClick={onCloseAudioset}>Cerrar audioset</Button>
  </div>
);
HasSession.propTypes = {
  onCloseAudioset: PropTypes.func
};

export default HasSession;
