import { createStore, compose, applyMiddleware } from "redux";
// import thunk from "redux-thunk";
import { throttle } from "lodash";
import { loadState, saveState } from "../browser/localStorage";

import reducer, { INIT_SIDE_EFFECTS } from "./model";
import effects from "./effects";

/**
 * Create an store
 */
export default function() {
  const initialState = loadState();

  const fxLen = effects.length;
  const effectsMiddleware = store => next => action => {
    // state is updated first!
    next(action);
    for (let i = 0; i < fxLen; i++) effects[i](action, store);
  };

  const devTools = window.__REDUX_DEVTOOLS_EXTENSION__
    ? window.__REDUX_DEVTOOLS_EXTENSION__()
    : f => f;
  const enhancer = compose(
    applyMiddleware(effectsMiddleware),
    devTools
  );

  const store = createStore(reducer, initialState, enhancer);
  store.dispatch({ type: INIT_SIDE_EFFECTS });

  store.subscribe(
    throttle(() => saveState(getStateToSave(store.getState())), 1000)
  );
  return store;
}

function getStateToSave({ playback, sync }) {
  return { playback, sync };
}
