import { throttle } from "lodash";
import * as screen from "screenfull";
import {
  SHOW_FULLSCREEN,
  INIT_SIDE_EFFECTS,
  setFullscreen,
  setSize
} from "../model";

function init(dispatch) {
  if (screen.enabled) {
    screen.on("change", () => dispatch(setFullscreen(screen.isFullscreen)));
  }

  const resize = throttle(
    () => dispatch(setSize(window.innerWidth, window.innerHeight)),
    1000
  );
  window.addEventListener("resize", resize);
  resize();
}

export default function(action, { dispatch }) {
  switch (action.type) {
    case SHOW_FULLSCREEN:
      screen.enabled && screen.request();
      break;

    case INIT_SIDE_EFFECTS:
      init(dispatch);
      break;

    default:
      break;
  }
}
