import React from "react";
import { connect } from "react-redux";
import { setAudioset } from "../../playback/actions";
import { loadAudioset, createRoom, joinRoom } from "../../store/model";
import NoSession from "./NoSession";
import HasSession from "./HasSession";
import Panel from "../shared/Panel";

const Session = ({
  audioset,
  audiosetIndex,
  openAudioset,
  closeAudioset,
  room,
  rooms,
  requestRoom,
  joinRoom
}) => {
  return (
    <Panel id="Session">
      <Panel.Header>
        <h1>Bienvenidx a Antropoloops</h1>
      </Panel.Header>
      <Panel.Content>
        {audiosetIndex && !audioset && (
          <NoSession
            audiosetIndex={audiosetIndex}
            onOpenAudioset={openAudioset}
            rooms={rooms}
            onJoinRoom={joinRoom}
          />
        )}

        {audioset && (
          <HasSession
            audioset={audioset}
            room={room}
            onRequestRoom={requestRoom}
            onCloseAudioset={closeAudioset}
          />
        )}
      </Panel.Content>
    </Panel>
  );
};

const mapStateToProps = state => ({
  audioset: state.playback.audioset,
  audiosetIndex: state.session.audiosetIndex,
  room: state.sync.room,
  rooms: state.sync.roomList
});
const mapDispatchToProps = dispatch => ({
  openAudioset: audiosetId => dispatch(loadAudioset(audiosetId)),
  closeAudioset: () => dispatch(setAudioset(null)),
  requestRoom: () => dispatch(createRoom()),
  joinRoom: room => dispatch(joinRoom(room))
});
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Session);
