import React from "react";
import PropTypes from "prop-types";
import { Link } from "../shared";

const NoSession = ({ audiosetIndex, rooms, onOpenAudioset, onJoinRoom }) => (
  <div>
    <h2>Empieza una nueva remezcla</h2>
    {audiosetIndex.map(set => (
      <Link key={set.id} onClick={() => onOpenAudioset(set.id)}>
        {set.meta.title}: {set.meta.description}
      </Link>
    ))}
    <h2>Conéctate a una en marcha</h2>
    {rooms.length
      ? rooms.map(room => (
          <Link key={room.id} onClick={() => onJoinRoom(room)}>
            {room.name}
          </Link>
        ))
      : "No hay otras sesiones activas en este momento."}
  </div>
);
NoSession.propTypes = {
  audiosetIndex: PropTypes.array,
  onOpenAudioset: PropTypes.func
};

export default NoSession;
