export const PLAYBACK_PREFIX = "[playback] ";
const name = name => PLAYBACK_PREFIX + name;

// Audioset

export const SYNC_PLAYBACK = name("SYNC_PLAYBACK");
export const syncPlayback = playback => ({ type: SYNC_PLAYBACK, playback });
export const setAudioset = audioset => ({
  type: SYNC_PLAYBACK,
  playback: { audioset }
});

// Pads
export const PRESS_PAD = name("PRESS_PAD");
export const pressPad = clipId => ({ type: PRESS_PAD, clipId });
export const RELEASE_PAD = name("RELEASE_PAD");
export const releasePad = clipId => ({ type: RELEASE_PAD, clipId });

// Audio
export const START_AUDIO_CLIP = name("START_AUDIO_CLIP");
export const startAudioClip = (clipId, trackId, time) => ({
  type: START_AUDIO_CLIP,
  clipId,
  trackId,
  status: { isPlaying: true, time }
});
export const STOP_AUDIO_CLIP = name("STOP_AUDIO_CLIP");
export const stopAudioClip = (clipId, trackId, time) => ({
  type: STOP_AUDIO_CLIP,
  clipId,
  trackId,
  status: { isPlaying: false, time }
});

export const SET_AUDIO_STARTED = name("SET_AUDIO_STARTED");
export const setAudioStarted = time => ({
  type: SET_AUDIO_STARTED,
  time
});
export const SET_AUDIO_STOPPED = name("SET_AUDIO_STOPPED");
export const setAudioStopped = time => ({
  type: SET_AUDIO_STOPPED,
  time
});

export const AUDIO_STOP_ALL = name("AUDIO_STOP_ALL");
export const stopAudio = () => ({
  type: AUDIO_STOP_ALL
});

// Tracks
export const CHANGE_TRACK_VOLUME = name("CHANGE_TRACK_VOLUME");
export const changeTrackVolume = (trackId, volume) => ({
  type: CHANGE_TRACK_VOLUME,
  trackId,
  volume
});
