import {
  SYNC_PLAYBACK,
  PRESS_PAD,
  RELEASE_PAD,
  AUDIO_STOP_ALL
} from "../../playback/actions";
import { INIT_SIDE_EFFECTS, TOGGLE_IS_CONTROL } from "../model";

// it will change with code-split
import Audio from "../../audio";
let audio = null;

export default function effects(action, { getState, dispatch }) {
  const state = getState();
  const enabled = !state.session.isControl;

  switch (action.type) {
    case INIT_SIDE_EFFECTS:
      audio = new Audio(dispatch, enabled);
      enabled && audio.restoreState(state.playback);
      break;

    case TOGGLE_IS_CONTROL:
      audio.setEnabled(enabled);
      break;

    case SYNC_PLAYBACK:
      enabled && audio.restoreState(action.playback);
      break;

    case PRESS_PAD:
      return enabled && audio.playSample(action.clipId);

    case RELEASE_PAD:
      return enabled && audio.stopSample(action.clipId);

    case AUDIO_STOP_ALL:
      return enabled && audio.stopAll();

    default:
  }
}
