import React from "react";
import cxs from "cxs/component";
import Toolbar from "../Toolbar";
import Session from "../Session";
import Pads from "../Pads";
import Tracks from "../Tracks";
import Keyboard from "../Keyboard";
import Modal from "react-modal";
import Help from "./Help";

export default props =>
  props.display.type === "mobile" ? (
    <Mobile {...props} />
  ) : (
    <Desktop {...props} />
  );

const Mobile = ({ audioset, display }) => (
  <Main>
    {audioset && <Toolbar />}
    <Content>{display.session ? <Session /> : <Pads />}</Content>
  </Main>
);

const Desktop = ({ audioset, display, toggleSession, hideHelp }) => {
  const notFull = !display.fullscreen;

  return (
    <>
      {audioset && (
        <Main className="App">
          <Help run={display.help} onStop={hideHelp} />
          {notFull && <Toolbar />}
          <Content>
            {notFull && display.pads && <Pads />}
            <Center />
            {notFull && display.tracks && <Tracks />}
          </Content>
          {notFull && <Keyboard />}
        </Main>
      )}
      <Modal
        isOpen={!audioset || display.session}
        shouldCloseOnOverlayClick={true}
        onRequestClose={toggleSession}
        style={ModalStyles}
      >
        <Session />
      </Modal>
    </>
  );
};

const ModalStyles = {
  overlay: {
    backgroundColor: "rgba(0, 0, 0, 0.5)",
    zIndex: 900
  },
  content: {
    // http://lynn.io/2014/02/22/modalin/
    position: "fixed",
    maxHeight: "calc(100% - 100px)",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",

    // position: "absolute",
    // top: "40px",
    // left: "0px",
    // right: "auto",
    // bottom: "40px",
    border: "",
    background: "black",
    borderRadius: 0,
    zIndex: 1000
  }
};

const Main = cxs("div")({
  display: "flex",
  flexDirection: "column",
  minHeight: "100vh"
});

const Content = cxs("div")({
  display: "flex",
  flexGrow: 1,
  flexDirection: "row",
  height: "100%",
  minHeight: "100%",
  maxHeight: "100%",
  overflow: "hidden"
});

const Center = cxs("div")({
  flexGrow: 1
});
